import React from "react";
import { StaticQuery, graphql, Link } from "gatsby";
import Img from "gatsby-image";

import Helmet from "../components/Helmet";

export default () => (
    <StaticQuery
        query={graphql`
            query {
                file(relativePath: { eq: "page-not-found.png" }) {
                    childImageSharp {
                        fluid {
                            ...GatsbyImageSharpFluid_noBase64
                        }
                    }
                }
            }
        `}
        render={data =>
            <div css={{
                maxWidth: 720,
                margin: "0 auto",
                padding: 25,
                "p a": {
                    color: "var(--green)",
                },
            }}>
                <Helmet
                    title="404"
                    description="Error, page not found."
                    robots="none"
                />
                <Img
                    fluid={ data.file.childImageSharp.fluid }
                    style={{
                        margin: "0 auto",
                        maxWidth: 450,
                    }}
                />
                <div css={{
                    textAlign: "center",
                }}>
                    <div css={{
                        fontSize: "2em",
                        fontWeight: 700,
                        marginBottom: 25,
                    }}>
                        Page Not Found
                    </div>
                    <div css={{
                        marginBottom: 25,
                        color: "lightgrey",
                        fontWeight: 500,
                    }}>
                        Hey, looks like you're in some uncharted territory. Let's head back <Link to="/">home</Link>.
                    </div>
                </div>
            </div>
        }
    />
);
